body {
    color: black;
    background-color: white;
    font-family: 'Montserrat';
}
/* NAVBAR */

.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}
.navbar a {
    font-size: 18px;
    text-decoration: none;
    margin: 15px;
    letter-spacing: .2em;
    color: black;
}
.navbar h1 {
}
