nav a:hover {
    background-color: lightblue;
    color: blue;
}
.highlight-link {
    color: orange;
}

nav h1 {
    margin: 15px auto 15px 15px;
    font-size: 18px;
    letter-spacing: .2em;
}

/* HOME */

.home-page .about-contact-columns {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.home-page .about-me {
    width: 500px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 24px;
    margin: 30px 15px 30px 30px;
}

.home-page .contact-me {
    width: 360px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 24px;
    margin: 50px 30px 30px 15px;
}
.home-page .contact-me  a:first-of-type {
    margin: 10px 0px 0px 0px;
 }

.home-page .contact-me a {
    -ms-flex-item-align: left;
        align-self: left;
    text-decoration: none;
    color: black;
}
.home-page .contact-me a:visited {
    color: black;
}







/* SUBHEADER */

.sub-header{
    background-color: #F5F8FA;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
.sub-header img {
    max-height: 200px;
    max-width: 200px;
    height: auto;
    width: auto;
    border-radius: 50%;
    padding: 10px;
    background: white;
}

/* PROJECTS PAGE */
.projects-page .project-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.projects-page img {
    max-height: 400px;
    width: auto;
    height: auto;
    border-radius: 50%;
}




